<template>
  <div style="width:100%;height:100%;">
    <!-- <div @click="asd()" class="top">aa</div> -->

    <!-- 搜索框 -->
    <div class="top-box" style="background: #F12A3E;">
      <div style="width:100%;">
        <div class="zhc-top-jt">
          <img class="zhc-top-img" src="../img/home-back@2x.png" @click="back" />
        </div>
        <div class="zhc-top-zi">Station selection</div>
      </div>
    </div>
    <div style="width:100%;background: #F12A3E;">
      <el-row class="demo-autocomplete">
        <el-col :span="12" id="asas">
          <el-autocomplete
            class="inline-input"
            autofocus
            v-model="state2"
            :fetch-suggestions="querySearch"
            placeholder="Please enter the city or station "
            :trigger-on-focus="false"
            @select="handleSelect"
          ></el-autocomplete>
        </el-col>
      </el-row>
    </div>
    <div class="zjre">
      <div class="zj-top">Recently used</div>

      <div class="zj-zj">
        <div class="remenk" v-for="(item,index) in shi" :key="index" @click="lishi(item)">{{item}}</div>
      </div>
      <div class="zj-top">Hot station</div>
      <div class="zj-zj">
        <div
          @click="remend(index,$event)"
          class="remenk"
          v-for="(item,index) in remen"
          :key="index"
        >{{item.Pingyin}}</div>
      </div>
    </div>

    <!-- <mt-index-list>
            <mt-index-section v-for="(item,index) in cityData" :key="index" :index="item.name">
                <mt-cell v-for="(ii,index) in item.cities" :key="index" :title="ii.tags"></mt-cell>
            </mt-index-section>
    </mt-index-list>-->
  </div>
</template>
<style>
#asas {
  width: 95%;
}
.el-col-12 {
  width: 100%;
}
.top {
  width: 100%;
  height: 200px;
}
.mint-cell-wrapper {
  font-size: 10px;
}
.mint-indexsection-index {
  font-size: 12px;
}
.zjre {
  width: 100%;
  /* background: yellowgreen; */
}
.zj-top {
  width: 95%;
  height: 58px;
  margin: 0 auto;
  line-height: 58px;
  font-size: 15px;
}
.top-box {
  width: 100%;
  /* height:100px; */
  background: #f12a3e;
}
.zj-zj {
  width: 95%;
  height: 120px;
  margin: 0 auto;
  /* background: #F12A3E; */
  overflow: hidden;
}
.remenk {
  width: 108px;
  height: 30px;
  background: #f2f2f4;
  border-radius: 4px;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  display: inline-block;
  margin: 5px;
}
.el-autocomplete {
  width: 100%;
}
.el-input__inner {
  width: 95%;
  height: 30px;
  margin-left: 2.5%;
}

.zhc-top-jt {
  width: 20%;
  height: 20%;
  display: inline-block;
  line-height: 50px;
  float: left;
}
.zhc-top-img {
  width: 22px;
  line-height: 30px;
  margin-left: 10%;
}
.zhc-top-zi {
  width: 50%;
  height: 50%;
  margin: 0 auto;
  text-align: center;
  color: #ffffff;
  display: inline-block;
  line-height: 30px;
  font-size: 18px;
  margin-left: -20%;
}
</style>
<script>
import Vue from "vue";
import { IndexList, IndexSection } from "mint-ui";

Vue.component(IndexList.name, IndexList);
Vue.component(IndexSection.name, IndexSection);
export default {
  data() {
    return {
      value2: "",
      result: [],
      cc: [
        "a",
        "b",
        "c",
        "d",
        "e",
        "f",
        "g",
        "h",
        "i",
        "j",
        "k",
        "l",
        "m",
        "n",
        "o",
        "p",
        "q",
        "r",
        "s",
        "t",
        "u",
        "v",
        "w",
        "x",
        "y",
        "z"
      ],
      a: [],
      b: [],
      c: "",
      newCityList: [],
      zaishi: [],
      laji: [],
      ming: [],
      restaurants: [],
      state1: "",
      state2: "",
      remen: [],
      jl: [],
      shi: [],
      historyWord: "",
      current: 0,
      suopiny: "",
      zhenpy: "",
    };
  },
  created() {
    this.historyWord = localStorage.getItem("searchWord");
    this.historyWord = this.historyWord.split(","); //将字符串转成数组
  },
  mounted() {
    var a = sessionStorage.getItem("jl");
    // var c = JSON.parse(a)
    // console.log(c)
    this.jl.push(sessionStorage.getItem("jl"));
    this.shi.unshift(this.jl[0]);
    var b = [];
    this.$http
      .get(
        "http://123.57.18.91:1002/api/TrainStation?terminal={terminal}&time_stamp={time_stamp}&random={random}&sign={sign}"
      )
      .then(function(res) {
        console.log(res);
        this.a = res.body.data;
        console.log(res.body.data);
        // console.log(that.a)
        // for(var i=0;i<this.a.pinyin.length;i++){
        //     for(var g=0;g<this.cc.length;g++)
        //     if(this.a[i].pinyin.slice(0,1)==this.cc[g]){
        //         this.c=this.a[i].pinyin
        //     }
        // }
        let map = {},
          dest = [];
        for (let i = 0; i < this.a.length; i++) {
          // var shi = this.a[i].pinyin.slice(0,1)
          let ai = this.a[i];
          if (!map[ai.pinyin.slice(0, 1)]) {
            dest.push({
              cityTp: ai.pinyin.slice(0, 1),
              data: [ai]
            });
            // map[ai.pinyin.slice(0,1)] = ai;
          } else {
            // for (let j = 0; j < this.cc.length; j++) {
            //     let dj = this.cc[j];
            //     if (dj === ai.cityTp) {
            //         dj.push(ai);
            //         break;
            //     }
            // }
          }
        }
        this.newCityList = dest;

        for (var i = 0; i < this.a.length; i++) {
          this.ming.push({ value: this.a[i].pinyin, ind: this.a[i].code });
        }
      });
    // 循环
    this.restaurants = this.loadAll();
    this.$http
      .get(
        "http://123.57.18.91:1002/api/ticket/queryHotCity?terminal={terminal}&time_stamp={time_stamp}&random={random}&sign={sign}"
      )
      .then(function(res) {
        this.remen = res.data.data;
      });
    // for(var i = 0;i<this.remen.length;i++){
    //     this.remen.splice(this.remen[i],1)
    // }
  },
  methods: {
    back() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    lishi(item) {
      console.log("123");
      console.log(item);
    },
    // 热门城市点击
    remend(index, event) {
      console.log(event);
      this.current = index;
      //获取点击对象
      var el = event.currentTarget;

      // console.log(this.remen[this.current])
      for (var c = 0; c < this.a.length; c++) {
        console.log(this.remen[this.current].Pingyin);
        if (this.a[c].name == this.remen[this.current].CityName) {
          this.suopiny = this.a[c].code;
          this.zhenpy = this.a[c].pinyin;
        }
      }
      this.itemClickHandler({ value: this.zhenpy, ind: this.suopiny });
      // console.log(this.current)
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    loadAll() {
      return this.ming;
    },
    handleSelect(item) {
      this.$router.push({
        //核心语句
        path: "/", //跳转的路径
        query: {
          //路由传参时push和query搭配使用 ，作用时传递参数
          chufa: item.value,
          chufacode: item.ind
        }
      });
      console.log(item);

      sessionStorage.setItem("jl", item.value);
    },
    getResult(val) {
      if (!val) return [];
      console.log(this.ming);
      return this.ming.filter(value => new RegExp(val, "i").test(value)); //console.log(this.ming)
    },
    itemClickHandler(item) {
      // this.$dialog.toast({mes: `搜索：${item}`});
      this.$router.push({
        //核心语句
        path: "/", //跳转的路径
        query: {
          //路由传参时push和query搭配使用 ，作用时传递参数
          chufa: item.value,
          chufacode: item.ind
        }
      });
    },
    submitHandler(value) {
      // this.$dialog.toast({mes: `搜索：${value}`});
    },

    asd: function() {}
  },
  watch: {
    value2(val) {
      // this.result = this.getResult(val);
    }
  }
};
</script>



