<template>
<!--车票预订添加乘客-->
  <div class="cheke-box" style="background:#ffffff;height:700px;">
      <div class="toubu">
          <img @click="huitui" class="huituian" src="../img/find train-back.png" alt="">
          <span class="kaitouwen">Choose passenger</span>
          <span @click="tianjia" id="queding">Complete</span>
      </div>
      <!-- 成人 -->
      <van-checkbox-group v-model="result">
        <van-checkbox v-for="(item, index) in list" :key="index" :name="item" shape="square" checked-color="#1F97BA">
            <!-- 复选框 {{ item }} -->
            <div class="chengke">
                <div class="xuanzhong"></div>
                <div class="keziliao">
                    <div class="mingzi">
                        {{item.qname}} {{item.name}}
                        <span class="biao">{{item.chengren}}</span>
                    </div>
                    <span class="idhao">
                            ID Number {{item.id}}
                    </span>
                    <span class="guo">
                        {{item.guo}}
                    </span>
                </div>
            </div>
        </van-checkbox>
        </van-checkbox-group>
        <!-- 儿童 -->
        <van-checkbox-group v-model="resultt">
        <van-checkbox v-for="(item, index) in listt" :key="index" :name="item" shape="square" checked-color="#1F97BA">
            <!-- 复选框 {{ item }} -->
            <div class="chengke">
                <div class="xuanzhong"></div>
                <div class="keziliao">
                    <div class="mingzi">
                        {{item.qname}} {{item.name}}
                        <span class="biao">{{item.chengren}}</span>
                    </div>
                    <span class="idhao">
                            ID Number {{item.id}}
                    </span>
                    <span class="guo">
                        {{item.guo}}
                    </span>
                </div>
            </div>
        </van-checkbox>
        </van-checkbox-group>


        <div class="tianjiacheng">
            <span @click="tianchengren">Add adult</span>
                <span></span>
            <span @click="tianjiaertong">Add child</span>
        </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Checkbox, CheckboxGroup } from 'vant';

Vue.use(Checkbox).use(CheckboxGroup);
export default {
    data(){
        return {
            result:[],
            resultt:[],
            list:[],
                asd:[],
                listt:[]
        }
    },
    methods:{
        huitui(){
            this.$router.go(-1)
        },
        // 添加到买票
        tianjia(){
            this.$router.push({
                path:"/zhaochea",
                query:{
                    result:this.result,
                    resultt:this.resultt
                }
            })
            console.log(this.result)
            console.log(this.resultt)
        },
        tianchengren(){
            this.$router.push({
                path:"/tianjiachengren",
                query:{
                    list:this.list,
                    listt:this.listt
                }
            })
        },
        tianjiaertong(){
            this.$router.push({
                path:"/tianjiaertong",
                query:{
                    listt:this.listt,
                    list:this.list
                }
            })
        }
    },
    created(){
        // this.list=this.$route.query.xinxi
        // console.log(this.list)
    },
    mounted(){
        console.log(this.result)
        // var e=sessionStorage.getItem("xinxi")


            // var dd={name:this.$route.query.name,qname:this.$route.query.qname,id:this.$route.query.id,guo:this.$route.query.guo}
        // var d=sessionStorage.setItem("xinxi",c+','+dd)
        // var c=sessionStorage.getItem("xinxi")
            // this.list.push(dd)
            // this.asd=this.list
            // this.list=this.asd
        // var cc=localStorage.setItem("ccc",this.list)
        // var c=localStorage.getItem("ccc")
        // this.list=c
        // this.list.push(e)
        // console.log(this.$route.query.xinxi)
        // this.list=this.$route.query.xinxi
        console.log(this.$route.query.xinxi)
        if(sessionStorage.getItem("list")==undefined||sessionStorage.getItem("list")=="undefined"){
            
            this.list=this.$route.query.xinxi
            console.log(this.list)
            sessionStorage.setItem("list",this.list)
        }else{
            this.list=this.$route.query.xinxi
            sessionStorage.setItem("list",this.list)
            console.log(this.list)
            var a=sessionStorage.getItem("list")
            console.log(a)
        }

        if(sessionStorage.getItem("listt")==undefined||sessionStorage.getItem("listt")=="undefined"){
            // console.log(sessionStorage.getItem("listt"))
            // this.listt=this.$route.query.xinxii

            this.listt=this.$route.query.xinxii
            sessionStorage.setItem("listt",this.listt)
        }else{
            // sessionStorage.setItem("listt",this.$route.query.xinxii)
            // this.listt=sessionStorage.getItem("listt")
            // console.log(this.listt)
            this.listt=this.$route.query.xinxii
            sessionStorage.setItem("listt",this.$route.query.xinxii)
            console.log(this.listt)
            // this.list=sessionStorage.getItem("list")
            console.log(sessionStorage.getItem("listt"))
        }
        if(this.list==undefined){
            var lisss = sessionStorage.getItem("lii")
            this.list=JSON.parse(lisss)
        }
        if(this.listt==undefined){
            var lsss = sessionStorage.getItem("liii")
            this.listt=JSON.parse(lsss)
        }
        


    },
    beforeDestroy(){
        
        var lii = JSON.stringify(this.list)
        sessionStorage.setItem("lii",lii)
        var liii = JSON.stringify(this.listt)
        sessionStorage.setItem("liii",liii)
        console.log(this.resultt)
    }
}
</script>

<style>
@import "../css/tianjia.css";
</style>


