<template>
  <div>
    <div  class="a">
      <span @click="huitui">
        <img
          style="width:22px;float:left;margin-top:17px;margin-left:10px;"
          src="../../img/find train-back.png"
        />
      </span>
      <div style="clear:both"></div>
    </div>
    <div class="b">
      <p>我是第一个页面</p>
    </div>
    
  </div>
</template>
<script>
export default {
  methods: {
    huitui() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/help" });
        return false;
      } else {
        this.$router.go(-1);
      }
    }
  }
};
</script>
<style>
.a{
  position: relative;
}
.b{
  width: 100px;
  background-color: red;
}
.b p{
  position: absolute;
  top:60px;
  left:100px;
  font-size:20px;
  
}
</style>


