<template>
  <div style="width:100%;height:700px;background:#ffffff;">
      <div class="qxtop">
          <img @click="huitui" style="width:22px;margin-left:2.5%;float:left;margin-top:16px;" src="../img/find train-back.png" alt="">
          <span style="font-size:14px;">Cancel</span>
      </div>
      <div style="width:95%;font-size:16px;margin:0 auto;margin-top:20px;">Tracking No.: {{weiyi}}</div>
      <div style="width:95%;font-size:16px;margin:0 auto;border-left:1px solid #1F97BA;margin-top:10px;">
          <yd-cell-item>
            <yd-input slot="right" v-model="input1" max="" placeholder="Cancel details:"></yd-input>
            </yd-cell-item>
      </div>
      <div @click="shanchu" class="dianjiquxiaoanniu">Submit</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        input1:"",
        item:"",
        weiyi:"",
        tuipiao:"",
        id:""
    }
  },
  mounted(){
      this.item = this.$route.query.item
      console.log(this.item)
      for(var i=0;i<this.item.passengers.length;i++){
          this.weiyi=this.item.passengers[i].ticket_no
      }
  },
  methods: {
      huitui(){
          this.$router.go(-1)
      },
      shanchu(){
          for(var i=0;i<this.item.passengers.length;i++){
                this.id=this.item.passengers[i].passengerid
            }
          var tuipiao = {
                "order_number": this.item.order_number,
                "refund_passanger_ids": this.id,
                "refund_reason": this.input1,
                "terminal": 1,
                "time_stamp": timestamp,
                "random": bb,
                "sign": ""
            }
          function getRandomInt(min, max) {
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }
           
            var bb=getRandomInt(10000, 99999)
            var timestamp=new Date().getTime()
            // thant.$http.post("http://123.57.18.91:1002/api/order/orderStatusJH", thant.tijiaoshu,{emulateJSON:true}).then(function(jh){
            //     console.log(jh)
            // })
            this.$http.post("http://123.57.18.91:1002/api/ticket/refund", tuipiao,{emulateJSON:true}).then(function(rs){
                            console.log(rs)
                            if(rs.body.code==1){
                                this.$router.push({
                                    path:"/"
                                })
                            }
                            // this.$route.query.Email
                            // this.$route.query.PhoneNumber
                            this.$http.get("http://123.57.18.91:1002/api/order/queryV2?order_number="+tuipiao.order_number+"&email="+"15220574@qq.com"+"&phone_number="+8615101654743+"&terminal=1&time_stamp="+timestamp+"&random="+bb+"&sign={sign}").then(function(res){
                                console.log(res)
                            })
                        })
      }
  },
  components: {

  }
}
</script>

<style>
.qxtop{
    width:100%;
    height:50px;
    line-height: 50px;
    text-align: center;
    background: #ffffff;
    border-bottom: 1px solid #DCDCDC;
}
.dianjiquxiaoanniu{
    width:95%;
    height:44px;
    border-radius: 4px;
    background:linear-gradient(87deg,rgba(255,67,81,1),rgba(237,20,36,1));
    color:#FFFFFF;
    margin:0 auto;
    margin-top:30%;
    text-align: center;
    line-height: 44px;
}
</style>



