<template>
  <div>
    <div class="toptop">
      <img @click="hui" style="width:22px;float:left;margin-top:17px;" src="../img/find train-back.png" >
      <span style="font-size:18px;">
        Deliver to my China hotel
      </span>
      <span @click="baocun" style="color:#1F97BA;font-size:14px;float:right;">Save</span>
    </div>
    <van-cell-group>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="name" placeholder="* Hotel Name" />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="dizhi" placeholder="* Hotel Address" />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="dianhua" placeholder="* Hotel Phone No." />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="ydname" placeholder="* Booking Name" />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="ruzhu" placeholder="* Check In Date" />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="banchu" placeholder="* Check Out Date" />
                </div>
                <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="wangzhan" placeholder="Booking Agency / Website" />
                </div>
            </van-cell-group>
  </div>
</template>

<script>
import Vue from 'vue'
import { Field } from 'vant';
Vue.use(Field);
import { Popup } from 'vant';
Vue.use(Popup);
export default {
  data(){
    return {
      name:"",
      dizhi:"",
      dianhua:"",
      ydname:"",
      ruzhu:"",
      banchu:"",
      wangzhan:"",
      jiudianzong:[]
    }
  },
  mounted(){
    console.log(typeof this.name)
  },
  methods:{
    hui(){
      this.$router.go(-1);
    },
    baocun(){
      if(this.name==""){
        alert("Hotel name cannot be empty")
      }else if(this.dizhi==""){
        alert("Hotel Address cannot be empty")
      }else if(this.dianhua==""){
        alert("Hotel Address cannot be empty")
      }else if(this.ydname==""){
        alert("Hotel Address cannot be empty")
      }else if(this.ruzhu==""){
        alert("Hotel Address cannot be empty")
      }else if(this.banchu==""){
        alert("Hotel Address cannot be empty")
      }else{
        this.jiudianzong=[{name:this.name,dizhi:this.dizhi,dianhua:this.dianhua,ydname:this.ydname,ruzhu:this.ruzhu,banchu:this.banchu,wangzhan:this.wangzhan}]
        this.$router.push({
          path:"/zhaochea",
          query:{
            jiudianzong:this.jiudianzong 
          }
        })
      }
    }
  }
}
</script>

<style>
  .toptop{
    width:100%;
    height:50px;
    border-bottom: 1px #DCDCDC solid;
    text-align: center;
    line-height: 50px;
    margin-bottom: 20px;
  }
</style>


