<template>
  <!--这是首界面上半部分-->
  <div class="btone-box">
    <!-- 最上方图片 -->
    <div class="bto-top">
      <div class="bto-top-img-hl">
        <span>
          <img class="bto-top-img" src="../img/home-back@2x.png" alt />
        </span>
        <span class="bto-top-hl" @click="helping">Help?</span>
      </div>
      <ul class="bto-ul">
        <li class="bto-c bto-c-1" @click="a">Air Ticket</li>
        <li style="background-color:#FFFFFF;" class="bto-c bto-c-2" @click="b">Train Ticket</li>
        <li class="bto-c bto-c-3" @click="c">China Tour</li>
      </ul>
    </div>
    <!-- 第一个显示票 -->
    <div class="bto-piao s1">
      <Flane></Flane>
    </div>
    <!-- 第二个显示票 -->
    <div class="bto-piao s2">
      <div class="s2-nei">
        <div>
          <span class="s2-s" @click="spa1">China Train</span>
        </div>
        <div>
          <span class="s2-ss" @click="spa2">International Train</span>
        </div>
      </div>
      <!-- 地址 -->
      <div class="bto-dz">
        <div class="dizhi diz1">
          <div class="zi">
            <router-link :to="{path:'/ceshi'}">
              <span class="vle1">beijing</span>
            </router-link>
            <img @click="zhuanh" class="bto-img" src="../img/diaohuan.png" />
            <router-link :to="{path:'/ceshier'}">
              <span class="vle2">shanghai</span>
            </router-link>
          </div>
          <div @click="selectYear" v-if="!isc" class="bto-zi">
            <div style="text-align:center">
              <span>{{month}}&nbsp;&nbsp;&nbsp;&nbsp;{{date}}</span>
              <span>{{we}}.</span>
            </div>
          </div>
          <!-- 这里是时间的效果 -->
          <mt-datetime-picker
            v-model="dateValue"
            type="date"
            ref="datePicker"
            year-format="{value} 年"
            month-format="{value} 月"
            date-format="{value} 日"
            :startDate="new Date()"
            @confirm="handleConfirm"
          ></mt-datetime-picker>
          <div class="bto-chek">
            <div class="chek-zi">Just book High-speed/Motor train</div>
            <input class="inpu" type="checkbox" id="awesome" />
            <label for="awesome">Awesome!</label>
          </div>
          <div @click="cha()" class="bto-bt" style="font-weight:normal;">Find Train</div>
        </div>

        <div class="dizhi diz2">
          <div class="zi">
            <router-link :to="{path:'/ceshi'}">
              <span class="vle1">beijing</span>
            </router-link>
            <img @click="zhuanh" class="bto-img" src="../img/diaohuan.png" />
            <router-link :to="{path:'/ceshier'}">
              <span class="vle2">shanghai</span>
            </router-link>
          </div>
        </div>
      </div>
      <img class="mstyy" src="../img/home-Customer service@2x.png" alt />
    </div>
    <!-- 第三个显示票 -->
    <div class="bto-piao s3">3</div>
    <!-- <div class="miaosimc">
        <img src="../img/home-Customer service@2x.png" alt="">
    </div>-->
  </div>
</template>
<style>
@import "../css/btone.css";
</style>
<script>
import Flane from "../components/flane";
import Vue from "vue";
// import { Toast } from 'mint-ui';
import { handleConfirm, DatetimePicker, Toast } from "mint-ui";
Vue.component(DatetimePicker.name, DatetimePicker);
export default {
  data() {
    return {
      dateValue: "",
      isc: false,
      year: "",
      month: "",
      date: "",
      isClicked: "",
      we: "",
      chufa: "",
      daoda: "",
      nyr: "",
      dqr: "",
      chufacode: "BJP",
      daodacode: "SHH",
      ll: "",
      cc: "",
      lo: ""
    };
  },
  created() {},
  components: {
    Flane
  },
  mounted() {
    var lo = window.location.href;
    this.lo = lo;
    // var loo = lo.indexOf("=",index+1)
    // var cc = lo.slice(loo+1,lo.length-2)
    // this.cc=cc

    var loo = lo.indexOf("=");
    var cc = lo.substring(loo + 1, lo.length);
    var yy = cc.indexOf("=");
    var bb = cc.substring(yy + 1, cc.length - 2);
    this.cc = bb;
    // alert("asd")
    // this.ll=this.$route.query.Cookie
    // alert(window.location.href)
    // var lo = location.href
    // var loo = lo.indexOf("=")
    // var cc = lo.slice(loo+1)
    // alert(cc)
    // sessionStorage.setItem("tcoke",cc)
    // 获取所有地点
    // 获取用车时间
    var date = new Date();
    var seperator1 = "-";
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    this.dqr = year + seperator1 + month + seperator1 + strDate;
    // 获取地点
    this.chufa = this.$route.query.chufa;
    this.chufacode = this.$route.query.chufacode;
    console.log(this.chufa);
    if (this.chufa !== undefined) {
      console.log("出发不等于undefined");
      document.getElementsByClassName("vle1")[0].innerHTML = this.chufa;

      sessionStorage.setItem("chufa", this.chufa);
      sessionStorage.setItem("chufacode", this.chufacode);
    }
    if (
      this.chufa == undefined &&
      sessionStorage.getItem("chufa") != undefined &&
      sessionStorage.getItem("chufa") != ""
    ) {
      console.log(typeof sessionStorage.getItem("chufa"));
      document.getElementsByClassName(
        "vle1"
      )[0].innerHTML = sessionStorage.getItem("chufa");
    }
    this.daoda = this.$route.query.daoda;
    this.daodacode = this.$route.query.daodacode;
    console.log(this.daoda);
    if (this.daoda != undefined) {
      document.getElementsByClassName("vle2")[0].innerHTML = this.daoda;
      sessionStorage.setItem("daoda", this.daoda);
      sessionStorage.setItem("daodacode", this.daodacode);
    } else if (
      this.daoda == undefined &&
      sessionStorage.getItem("daoda") != null
    ) {
      document.getElementsByClassName(
        "vle2"
      )[0].innerHTML = sessionStorage.getItem("daoda");
    }

    console.log("这是出发：" + sessionStorage.getItem("chufa"));
    console.log("这是出发：" + sessionStorage.getItem("chufacode"));

    console.log("这是到达：" + sessionStorage.getItem("daoda"));
    console.log("这是到达：" + sessionStorage.getItem("daodacode"));

    var value = new Date();
    var a = ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"];
    var yue = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sept",
      "Oct",
      "Nov",
      "Dec"
    ];
    (this.month = yue[value.getMonth()]),
      (this.date = value.getDate()),
      (this.we = a[value.getDay()]);
    document.getElementsByClassName("btone-box")[0].style.height =
      document.body.clientHeight;
    console.log();
    // console.log(document.body.offsetHeight)
    // console.log(document.body.scrollHeight)
    // console.log(window.screen.height)
    // console.log(window.screen.availHeight)
    // console.log(document.body.scrollTop)
    // console.log(window.screen.height)
  },
  methods: {
    helping() {
      this.$router.push({
        path: "/help"
      });
    },
    // 获取时间
    selectYear() {
      this.$refs.datePicker.open();
    },
    handleConfirm(value) {
      var a = ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"];
      console.log(value);
      this.year = value.getFullYear();
      var yue = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec"
      ];
      (this.month = yue[value.getMonth()]), (this.date = value.getDate());
      this.we = a[value.getDay()];
      console.log(this.we);
      this.isClicked = true;
      console.log(value.getDay());
      // this.nian=value.getFullYear()
      // this.yue=value.getMonth()+1
      // this.ri=value.getDate()
      var seperator1 = "-";
      var year = value.getFullYear();
      var month = value.getMonth() + 1;
      var strDate = value.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.nyr = year + seperator1 + month + seperator1 + strDate;

      console.log(this.nyr);
    },
    // 点击样式
    a() {
      document.getElementsByClassName("s1")[0].style.display = "block";
      var s1 = document.getElementsByClassName("bto-c-1")[0];
      s1.style.borderRadius = "5px 5px 0 0";
      s1.style.height = "50px";
      s1.style.backgroundColor = "#FFFFFF";
      s1.style.color = "#000";
      s1.style.opacity = "1";
      document.getElementsByClassName("s2")[0].style.display = "none";
      var s2 = document.getElementsByClassName("bto-c-2")[0];
      s2.style.borderRadius = "0px 0px 0 0";
      s2.style.height = "44px";
      s2.style.backgroundColor = "#262626";
      s2.style.color = "#FFFFFF";
      s2.style.opacity = "0.88";
      document.getElementsByClassName("s3")[0].style.display = "none";
      var s3 = document.getElementsByClassName("bto-c-3")[0];
      s3.style.borderRadius = "0px 5px 0 0";
      s3.style.height = "44px";
      s3.style.backgroundColor = "#262626";
      s3.style.color = "#FFFFFF";
      s3.style.opacity = "0.88";
    },
    b() {
      document.getElementsByClassName("s1")[0].style.display = "none";
      var s1 = document.getElementsByClassName("bto-c-1")[0];
      s1.style.borderRadius = "5px 0px 0 0";
      s1.style.height = "44px";
      s1.style.backgroundColor = "#262626";
      s1.style.color = "#FFFFFF";
      s1.style.opacity = "0.88";
      document.getElementsByClassName("s2")[0].style.display = "block";
      var s2 = document.getElementsByClassName("bto-c-2")[0];
      s2.style.borderRadius = "5px 5px 0 0";
      s2.style.height = "50px";
      s2.style.backgroundColor = "#FFFFFF";
      s2.style.color = "#000";
      s2.style.opacity = "1";
      document.getElementsByClassName("s3")[0].style.display = "none";
      var s3 = document.getElementsByClassName("bto-c-3")[0];
      s3.style.borderRadius = "0px 5px 0 0";
      s3.style.height = "44px";
      s3.style.backgroundColor = "#262626";
      s3.style.color = "#FFFFFF";
      s3.style.opacity = "0.88";
    },
    c() {
      document.getElementsByClassName("s1")[0].style.display = "none";
      var s1 = document.getElementsByClassName("bto-c-1")[0];
      s1.style.borderRadius = "5px 0px 0 0";
      s1.style.height = "44px";
      s1.style.backgroundColor = "#262626";
      s1.style.color = "#FFFFFF";
      s1.style.opacity = "0.88";
      document.getElementsByClassName("s2")[0].style.display = "none";
      var s2 = document.getElementsByClassName("bto-c-2")[0];
      s2.style.borderRadius = "0px 0px 0 0";
      s2.style.height = "44px";
      s2.style.backgroundColor = "#262626";
      s2.style.color = "#FFFFFF";
      s2.style.opacity = "0.88";
      document.getElementsByClassName("s3")[0].style.display = "block";
      var s3 = document.getElementsByClassName("bto-c-3")[0];
      s3.style.borderRadius = "5px 5px 0 0";
      s3.style.height = "50px";
      s3.style.backgroundColor = "#FFFFFF";
      s3.style.opacity = "1";
      s3.style.color = "#000";
    },
    spa1() {
      document.getElementsByClassName("s2-s")[0].style.color = "#F12A3E";
      document.getElementsByClassName("s2-s")[0].style.borderBottom =
        "2px #F12A3E solid";

      document.getElementsByClassName("s2-ss")[0].style.color = "#A0A0A0";
      document.getElementsByClassName("s2-ss")[0].style.borderBottom =
        "2px #A0A0A0 ";

      // 地址
      document.getElementsByClassName("diz1")[0].style.display = "block";
      document.getElementsByClassName("diz2")[0].style.display = "none";
    },
    spa2() {
      document.getElementsByClassName("s2-ss")[0].style.color = "#F12A3E";
      document.getElementsByClassName("s2-ss")[0].style.borderBottom =
        "2px #F12A3E solid";

      document.getElementsByClassName("s2-s")[0].style.color = "#A0A0A0";
      document.getElementsByClassName("s2-s")[0].style.borderBottom =
        "0.5px #A0A0A0 ";

      // 地址
      document.getElementsByClassName("diz1")[0].style.display = "none";
      document.getElementsByClassName("diz2")[0].style.display = "block";
    },
    zhuanh() {
      var vle1 = document.getElementsByClassName("vle1")[0];
      var vle2 = document.getElementsByClassName("vle2")[0];
      var a = vle1.innerHTML;
      var b = vle2.innerHTML;
      vle1.innerHTML = b;
      vle2.innerHTML = a;
      var chu = sessionStorage.getItem("chufacode");
      var dao = sessionStorage.getItem("daodacode");
      this.chufacode = dao;
      this.daodacode = chu;
      // console.log("这是dao："+dao)
      // console.log("这是chu："+chu)
      // console.log("这是出发:"+this.chufacode)
      // console.log("这是到达:"+this.daodacode)
    },
    // 查询余票
    cha() {
      document.getElementsByClassName("bto-bt")[0].innerHTML = "Loading";
      console.log(document.getElementsByClassName("bto-bt")[0].style.innerHTML);
      if (
        this.daodacode == undefined &&
        sessionStorage.getItem("daodacode") != undefined &&
        sessionStorage.getItem("daodacode") != null &&
        sessionStorage.getItem("daodacode") != ""
      ) {
        this.daodacode = sessionStorage.getItem("daodacode");
      }
      if (
        this.chufacode == undefined &&
        sessionStorage.getItem("chufacode") != undefined &&
        sessionStorage.getItem("chufacode") != null &&
        sessionStorage.getItem("chufacode") != ""
      ) {
        this.chufacode = sessionStorage.getItem("chufacode");
        console.log("chengg");
      }
      if (this.chufacode == undefined) {
        this.chufacode = "BJP";
        console.log("chengg");
      }
      if (this.daodacode == undefined) {
        this.daodacode = "SHH";
      }
      if (this.nyr == null || this.nyr == "") {
        console.log(this.chufacode);
        console.log(this.daodacode);
        this.$http
          .get(
            "http://123.57.18.91:1002/api/ticket/queryV2?train_date=" +
              this.dqr +
              "&from_station=" +
              this.chufacode +
              "&to_station=" +
              this.daodacode +
              "&terminal=1&time_stamp={time_stamp}&random=1234&sign={sign}"
          )
          .then(function(res) {
            if (res.body.code != 1) {
              Toast({
                message: "No eligible train information",
                duration: 3000
              });
            } else {
              sessionStorage.setItem(
                "chuming",
                document.getElementsByClassName("vle1")[0].innerHTML
              );
              sessionStorage.setItem(
                "daoming",
                document.getElementsByClassName("vle2")[0].innerHTML
              );
              this.$router.push({
                path: "/zhaohuoche",
                query: {
                  dqr: this.dqr,
                  chufacode: this.chufacode,
                  daodacode: this.daodacode,
                  we: this.we
                }
              });
            }
            console.log(res);
          });
      } else {
        console.log("hahah");
        this.$http
          .get(
            "http://123.57.18.91:1002/api/ticket/queryV2?train_date=" +
              this.nyr +
              "&from_station=" +
              this.chufacode +
              "&to_station=" +
              this.daodacode +
              "&terminal=1&time_stamp={time_stamp}&random=1234&sign={sign}"
          )
          .then(function(res) {
            if (res.body.code != 1) {
              Toast({
                message: "No eligible train information",
                duration: 3000
              });
            } else {
              sessionStorage.setItem(
                "chuming",
                document.getElementsByClassName("vle1")[0].innerHTML
              );
              sessionStorage.setItem(
                "daoming",
                document.getElementsByClassName("vle2")[0].innerHTML
              );
              this.$router.push({
                path: "/zhaohuoche",
                query: {
                  dqr: this.nyr,
                  chufacode: this.chufacode,
                  daodacode: this.daodacode
                }
              });
            }
            console.log(res);
          });
      }
    }
  }
};
</script>



