<template>
<!--火车票booking车票预订第二界面-->
  <div>
    <div class="xintoubu">
      <div style="width:100%;height:40px;display:flex;line-height:40px;">
        <div style="width:30%;height:100%;">
          <img style="height:50%;" src="../img/home-back@2x.png" alt="">
        </div>
        <div style="color:#ffffff;">Confirmation Details</div>
      </div>
      <div class="fangzh">
    <div class="piao-nei-s">
      <div class="chengkeid">
        <span style="width:68%;display:inline-block;">Tracking No.: </span>
        <span style="width:30%;display:inline-block;">Booking failure</span>
      </div>
    <div class="piao-top" id="xiangq">
          <div id="xianga">
              <span>{{sjz.from_station_name}}</span>
              <span>{{sjz.start_time}}</span>
              <span>{{this.ri}} {{this.rir}} {{this.xingqi}}</span>
          </div>

          <div style="width:25%;height:50%">
          <div class="xiangc">
              <div class="piao-d"></div>
              {{sjz.checi}}
              <div class="piao-d"></div>
          </div>
          <div class="xiangqinga">
              {{sjz.run_time}}
          </div>
          </div>
          
          <div class="xiangb">
              <span>{{sjz.to_station_name}}</span>
              <span>{{sjz.arrive_time}}</span>
              <span>{{this.ri}} {{this.rirr}} {{this.xiaxia}}</span>
          </div>
      </div>
      <div class="chengkepiao" v-for="(item,index) in sjz.passengers" :key="index">
        <div class="keziliao">
                    <div class="">
                        {{item.passengersename}}
                        <span class="biao">Adult</span>
                    </div>
                    <span class="idhao">
                            <span style="color:#1F97BA;">Passport</span> {{item.passportseno}}
                    </span>
                    <span class="guo">
                        end
                    </span>
                    <div class="qianshu">{{item.zwname}}  {{item.priceUSD}}</div>
                    <div style="width:100%;height:40px;">
                      <span style="display:inline-block;height:40px;line-height:40px;color:#F1951E;">Issuing</span>
                      <span style="margin-top:10px;height:19px;display:inline-block;float:right;color:#666666;border: 1px #DCDCDC solid;border-radius:4px;">Cancel</span>
                    </div>
                </div>
                
      </div>
    </div>
    <!-- 以下是服务费之类的东西 -->
      <div class="fuwua">
        <div class="liebiao">
          <div>
            <span>Adult train tickets x{{sjz.passengers.length}}</span> 
            <span style="float:right;">usd{{usd}}</span> 
          </div>
          <div>
            <span>Child train ticket x0</span> 
            <span style="float:right;">usd0</span> 
          </div>
          <div>
            <span>Service Fee x{{sjz.passengers.length}}</span> 
            <span style="float:right;">usd{{sjz.passengers.length*5}}</span> 
          </div>
          <div>
            <span>Courier fee x0</span> 
            <span style="float:right;">usd0</span> 
          </div>
        </div>
        <div style="display:flex;">
          <div style="width:50%;height:60px;line-height:60px;">Total amount payable:</div>
          <div style="width:50%;height:60px;line-height:60px;">USD{{usd+sjz.passengers.length*5}}</div>
        </div>
      </div>



</div>
    </div>
  </div>
</template>
<style>
@import "../css/shujuzhanshi";
</style>

<script>
export default {
  data() {
    return {
      sjz:this.$route.query.ss,
      usd:""
    }
  },
  mounted(){
      console.log(this.$route.query.ss)
      for(var i=0;i<this.sjz.passengers.length;i++){
        this.usd=this.sjz.passengers[i].priceUSD
      }
  },
  methods: {

  },
  components: {

  }
}
</script>

<style>

</style>



