<template>
  <div class="pay">
      <div class="fanhuitop">Pay</div>
      <div class="chonggtu">
          <img src="../img/支付成功@2x.png" alt="">
      </div>
      <div style="width:100%;font-size:21px;text-align:center;margin-top:30px;">Paid Successfully!</div>
      <div style="width:100%;font-size:15px;text-align:center;margin-top:26px;">Tracking order Note</div>
      <div style="width:80%;margin:0 auto;margin-top:18px;color:#999999;font-size:12px;">1. Book a train ticket departing within 30 days, the final status can be known within 24 hours after payment.</div>
      <div style="width:80%;margin:0 auto;margin-top:16px;color:#999999;font-size:12px;">2. Book a train ticket that is more than 30 days from the departure date, the final status can be known 29 days in advance.</div>
      <div class="queding" @click="tiaozhu">Check Order</div>
      <div>{{a}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        a:""
    }
  },
  methods: {
      tiaozhu(){
      if (window.history.length <= 1) {
        this.$router.push({ path: "/shujuzhanshi" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
         
  },
  mounted(){
      this.a = window.location.href
  },
  components: {

  }
}
</script>

<style>
.pay{
    background: #ffffff;
    height:700px;
}
.fanhuitop{
    width:100%;
    height:50px;
    /* background: #ffffff; */
    text-align: center;
    line-height: 50px;
    font-size: 18px;
}
.chonggtu{
    height:135px;
    width:100%;
    /* background: #ffffff; */
    text-align: center;
    margin-top:40px;
}
.chonggtu img{
    height:100%;
}
.queding{
    margin:0 auto;
    margin-top:73.5px;
    width:150px;
    height:44px;
    border-radius: 4px;
    background:#F12A3E;
    color:#ffffff;
    text-align: center;
    line-height: 44px;
    font-size: 15px;
}
</style>



