<template>
<!--付款界面-->
  <div class="btthree-box">
      <div class="booking-top">
        <div style="width:100%;height:50px;font-size:18px;text-align:center;line-height:50px;">Bookings</div>
        <div style="width:100%;height:40px;">
          <span class="o">Train ticket</span>
          <span class="t">Air ticket</span>
          <span class="h">China tour</span>
        </div>
        
      </div>
      <div class="zhuti">
          <img class="wusoutu" src="../img/无搜索结果@2x.png" alt="">
          <span style="display:block;margin:0 auto;color:#999999;font-size:15px;">No orders yet~~</span>
        </div>
      <div class="zhenzhuti">
          <div class="tijiaozong">
            <div class="tijiaori">{{piaoshuju.submit_time}} submit</div>
            <div class="tijiaopiaozhu" v-for="(item,index) in piaoshuju.passengers" :key="index">
              <!-- 票头 -->
              <div @click="piaoshushu(item)" style="width:100%;height:30%;color:#1F97BA;font-size:17px;">
                <span>{{piaoshuju.checi}}</span>
                <span style="color:#333333;margin-left:20px;">{{piaoshuju.from_station_name}}-{{piaoshuju.to_station_name}}</span>
                <span style="float:right;"><span style="font-size:12px;">usd</span>{{piaoshuju.orderamountUSD}}</span>
              </div>
              <!-- 票身 -->
              <div style="width:100%;">
                <span style="color:#666666;">
                  <div>Tracking No.:{{item.ticket_no}}</div>
                  <div>{{piaoshuju.train_date}}  {{piaoshuju.start_time}}-{{piaoshuju.arrive_time}}</div>
                </span>
              </div>
              <div style="width:100%;margin-top:10px;">
                <div @click="quxiao(piaoshuju)" class="dianjishanchu">Delete</div>
              </div>
            </div>
          </div>
          <!-- 以下是提交订单但是没有付钱的 -->
          <div class="tijiaozong">
            <div class="tijiaori">Reserve</div>
            <div class="tijiaopiaozhu" v-for="(item,index) in ding" :key="index">
              <!-- 票头 -->
              <div @click="shibai" style="width:100%;height:30%;color:#1F97BA;font-size:17px;">
                <span>{{item.checi}}</span>
                <span style="color:#333333;margin-left:20px;">{{item.from_station_name}}-{{item.to_station_name}}</span>
                <span style="float:right;"><span style="font-size:12px;">usd</span>{{item.orderamountUSD}}</span>
              </div>
              <!-- 票身 -->
              <div style="width:100%;">
                <span style="color:#666666;">
                  <!-- <div>Tracking No.:{{shibaipiao.ticket_no}}</div> -->
                  <div>{{item.train_date}}  {{item.start_time}}-{{item.arrive_time}}</div>
                </span>
              </div>
              <div style="width:100%;margin-top:10px;">
                <div class="dianjishanchu">付款</div>
                <div class="dianjishanchu">取消</div>
              </div>
            </div>
          </div>
          <!-- 以下是失败的 -->
          <div class="tijiaozong">
            <div class="tijiaori">Failure submit</div>
            <div class="tijiaopiaozhu" v-for="(item,index) in shibaipiao" :key="index" @click="shibai(item)">
              <!-- 票头 -->
              <div style="width:100%;height:30%;color:#1F97BA;font-size:17px;">
                <span>{{item.checi}}</span>
                <span style="color:#333333;margin-left:20px;">{{item.from_station_name}}-{{item.to_station_name}}</span>
                <span style="float:right;"><span style="font-size:12px;">usd</span>{{item.orderamountUSD}}</span>
              </div>
              <!-- 票身 -->
              <div style="width:100%;">
                <span style="color:#666666;">
                  <!-- <div>Tracking No.:{{shibaipiao.ticket_no}}</div> -->
                  <div>{{item.train_date}}  {{item.start_time}}-{{item.arrive_time}}</div>
                </span>
              </div>
              <div style="width:100%;margin-top:10px;">
                <div style="width:100%;color:red;text-align:center;font-size:14px;">Ticketing failed User information is incorrect</div>
              </div>
            </div>
          </div>
        </div>
        <yd-popup v-model="show1" position="center" width="90%">
            <div style="background-color:#fff;">
                <p>
                    If you have already purchased your ticket successfully or your booking is successful, please refresh this page repeatedly so that our staff has enough time to issue your ticket. If you have not booked yet, please quit this page to book your ticket.
                </p>
                
                <p style="text-align: center;">
                    <yd-button style="height:50px;" @click.native="show1 = false">Close the popup</yd-button>
                </p>
            </div>
        </yd-popup>
        <!-- <yd-button size="large" type="primary" @click.native="show1 = true"></yd-button> -->
  </div>
</template>
<style>
@import "../css/btthree.css";
</style>
<script>
export default {
  data(){
    return{
      tuipiao:"",
      chengkid:"",
      piaoshuju:"",
      shibaipiao:[],
      kehuxinxi:"",
      Email:"",
      PhoneNumber:"",
      idd:"",
      tijiaoshu:"",
      ding:[],
      show1:false
    }
  },
  mounted(){
    this.show1=true
    // sessionStorage.getItem("huiyuan")
    this.$http.get("http://123.57.18.91:1002/api/member/GetMemberInfo?token="+"13cb63ea-aa61-40ea-8bd5-74f6d05c4ba2").then(function(res){
                this.kehuxinxi=res.data.data
                console.log(res)
                    this.Email=res.data.data.Email
                    console.log(res.data.data.Email)
                    this.PhoneNumber=res.data.data.PhoneNumber
                    console.log(res.data.data.PhoneNumber)
                    this.idd=res.data.data.idd
            })
    function getRandomInt(min, max) {
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }
           
            var bb=getRandomInt(10000, 99999)
            var timestamp=new Date().getTime()
                            console.log(timestamp)
            console.log(this.Email)
            this.$http.get("http://123.57.18.91:1002/api/order/queryList?order_number={order_number}&email="+"15220574@qq.com"+"&phone_number="+8615101654743+"&terminal=1&time_stamp="+timestamp+"&random="+bb+"&sign={sign}").then(function(res){
                console.log(res)
                var rres = res.body.data
                for(var i=0;i<rres.length;i++){
                  // 判断是否支付完成
                  if(rres[i].status==1){
                    var zhifuod = rres[i].order_number
                    var tijiao = {
                          "order_number":zhifuod,
                          "email": "15220574@qq.com",
                          "phone_number": 8615101654743,
                          "terminal": 1,
                          "time_stamp": timestamp,
                          "random": bb,
                          "sign": ""
                      }
                      // 提交占座申请
                      this.$http.post("http://123.57.18.91:1002/api/order/submitNew", tijiao,{emulateJSON:true}).then(function(reees){
                        console.log(reees)
                      })
                  }else if(rres[i].status==2){
                    var zhifuod = rres[i].order_number
                    var tijiao = {
                          "order_number":zhifuod,
                          "email": "15220574@qq.com",
                          "phone_number": 8615101654743,
                          "terminal": 1,
                          "time_stamp": timestamp,
                          "random": bb,
                          "sign": ""
                      }
                    this.$http.post("http://123.57.18.91:1002/api/order/orderStatusJH", tijiao,{emulateJSON:true}).then(function(jh){
                      console.log(jh)
                    })
                  }else if(rres[i].status==3){
                    var zhifuod = rres[i].order_number
                    var tijiao = {
                          "order_number":zhifuod,
                          "email": "15220574@qq.com",
                          "phone_number": 8615101654743,
                          "terminal": 1,
                          "time_stamp": timestamp,
                          "random": bb,
                          "sign": ""
                      }
                      var chupiao = {
                          "order_number": zhifuod,
                          "terminal": 1,
                          "time_stamp": timestamp,
                          "random": bb,
                          "sign": ""
                      }
                    this.$http.get("http://123.57.18.91:1002/api/order/queryV2?order_number="+zhifuod+"&email="+"15220574@qq.com"+"&phone_number="+8615101654743+"&terminal=1&time_stamp="+timestamp+"&random="+bb+"&sign=").then(function(ce){
                        console.log(ce)
                        this.$http.post("http://123.57.18.91:1002/api/ticket/pay", chupiao,{emulateJSON:true}).then(function(rs){
                          console.log(rs)
                          if(rs.body.code==1){
                              alert("出票成功")
                          }
                        })
                      })
                  }else if(rres[i].status==5||rres[i].status==11){
                      document.getElementsByClassName("zhuti")[0].style.display="none"
                        var odnu =  rres[i].order_number
                        console.log(rres[i].order_number)
                        this.$http.get("http://123.57.18.91:1002/api/order/queryV2?order_number="+rres[i].order_number+"&email="+"15220574@qq.com"+"&phone_number="+8615101654743+"&terminal=1&time_stamp="+timestamp+"&random="+bb+"&sign={sign}").then(function(res){
                            console.log(res)
                            this.piaoshuju=res.body.data
                            console.log(this.piaoshuju)

                            var tijiao = {
                                                "order_number":res.body.data.order_number,
                                                "email": res.body.data.email,
                                                "phone_number": res.body.data.phone_number,
                                                "terminal": 1,
                                                "time_stamp": timestamp,
                                                "random": bb,
                                                "sign": ""
                                            }
                            // var thant = this
                            // setInterval(function(){
                                this.$http.post("http://123.57.18.91:1002/api/order/orderStatusJH", tijiao,{emulateJSON:true}).then(function(jh){
                                    console.log(jh.body.data)
                                    if(jh.body.data==5||jh.body.data==7){
                                        clearInterval()
                                        // alert("Clear")
                                    }
                                })
                                // },5000)

                            for(var a=0;a<res.body.data.passengers.length;a++){
                                if(res.body.data.passengers.length>1){

                                }else{
                                    this.chengkid = res.body.data.passengers[a].passengerid
                                    console.log(this.chengkid)
                                    // console.log(this.t)
                                    // console.log(this.bbb)
                                    this.tuipiao = {
                                        "order_number": odnu,
                                        "refund_passanger_ids": this.chengkid,
                                        "refund_reason": "taxi",
                                        "terminal": 1,
                                        "time_stamp": timestamp,
                                        "random": bb,
                                        "sign": ""
                                    }
                                }
                            }
                        })

                    }else if(rres[i].status==4){
                      document.getElementsByClassName("zhuti")[0].style.display="none"
                      console.log(rres[i])
                      this.shibaipiao.push(rres[i])
                      // var chupiao = {
                      //     "order_number": rres[i].order_number,
                      //     "terminal": 1,
                      //     "time_stamp": timestamp,
                      //     "random": bb,
                      //     "sign": ""
                      // }
                      // this.$http.post("http://123.57.18.91:1002/api/ticket/pay", chupiao,{emulateJSON:true}).then(function(rs){
                      //   console.log(rs)
                      // })
                    }else if(rres[i].status==1){
                      this.ding.push(rres[i])
                      console.log(this.ding)
                    }
                }
                for(var c=0;c<this.ding.length;c++){
                  console.log(this.ding[c])
                }
            })
  },
  methods:{
    quxiao(item){
      this.$router.push({
        path:"/quxiao",
        query:{
          item,
          Email:this.Email,
          PhoneNumber:this.PhoneNumber
        }
      })
    },
    shanchu(){
      this.shibaipiao=""
    },
    piaoshushu(item){
      this.$router.push({
        path:"/shujuzhanshi",
        query:{
          item,
          ss:this.piaoshuju
        }
      })
    },
    shibai(item){
      this.$router.push({
        path:"/shujuzhanshi",
        query:{
          ss:item
        }
      })
    }
  }
  
}
</script>



