<template>
<!--底部选项卡-->
  <div class="bag-box">
    <!-- <button @click="aaa">asd</button> -->
    <router-view></router-view>
    <div class="bag-bt">
      <div @click="shi" class="bag-bt-n shi">
        <router-link :to="{path:'/'}">
          <div class="img-dd">
            <img class="icoco" :src="shared" />
            <br />
            <span class="spp" style="color:#F12A3E;">Home</span>
          </div>
        </router-link>
      </div>
      <div @click="shii" class="bag-bt-n">
        <router-link :to="{path:'/three'}">
          <div class="img-d">
            <img class="icoco" :src="share" />
            <br />
            <span class="sppp" style="color:#1A1A1A;">Booking</span>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<style>
@import "../css/bag.css";
</style>
<script>
import one from "@/components/btone";
import tow from "@/components/bttow";
import three from "@/components/btthree";
import shared_nor from "../img/home-Selected@2x.png";
import shared_hover from "../img/icon-home-page.png";
import bookingOne from "../img/home-order@2x.png";
import bookingtwo from "../img/icon-news.png";
export default {
  data() {
    return {
      ll: "",
      kk: "",
      yy: "",
      shared: "",
      share: ""
    };
  },
  components: {
    one,
    tow,
    three
  },
  mounted() {
    (this.shared = shared_nor),
      (this.share = bookingOne),
      (document.getElementsByClassName("bag-box")[0].style.height =
        window.innerHeight);
    // this.yy=window.location.href
    var lo = window.location.href;
    // var loo = lo.indexOf("=",index+1)
    // var cc = lo.slice(loo+1,lo.length-2)

    var loo = lo.indexOf("=");
    var cc = lo.substring(loo + 1, lo.length);
    var yy = cc.indexOf("=");
    var bb = cc.substring(yy + 1, cc.length - 2);
    // this.cc=bb
    sessionStorage.setItem("huiyuan", bb);
    // this.yy = cc
    // this.ll=this.$route.query.Cookie
  },
  methods: {
    shi: function() {
      document.getElementsByClassName("spp")[0].style.color = "#F12A3E";
      this.shared = shared_nor;
      this.share=bookingOne;
      document.getElementsByClassName("sppp")[0].style.color =
        "rgb(26, 26, 26)";
    },
    shii: function() {
      document.getElementsByClassName("spp")[0].style.color = "rgb(26, 26, 26)";
      this.shared = shared_hover;
      this.share=bookingtwo;
      document.getElementsByClassName("sppp")[0].style.color = "#F12A3E";
    },
    aaa: function() {
      this.$router.push({
        path: "/pay"
      });
    }
  }
};
</script>
<style >
.img-dd {
  position: relative;
}
.ico {
  position: absolute;
  top: 1px;
  left: 88px;
}
</style>



