<template>
  <div>
      <div class="zhuzhi-top-box">
          <img style="width:22px;margin-left:2.5%;float:left;margin-top:16px;" src="../img/find train-back.png"  @click="huitui">
          <span style="font-size:14px;">Deliver to a personal address</span>
          <span @click="zhuzhi" style="float:right;color:#1F97BA;margin-right:2.5%;">Save</span>
      </div>
      <!-- 以下是输入框 -->
      <div style="width:100%;overflow: hidden;background:#FFFFFF;">
      <div style="width:95%;margin:0 auto;margin-top:20px;margin-bottom:500px;">
        <van-cell-group>
            <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="dizhiaaa" placeholder="* Delivery Address  Delivery Address Delivery 
Address" />
            </div>
            <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="nameaaa" placeholder="* Receiver's Name" />
            </div>
            <div style="border-bottom:1px #DCDCDC solid;">
                <van-field v-model="dianhuaaa" placeholder="* Phone Number" />
            </div>
        </van-cell-group>
       </div>
       </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Field } from 'vant';
Vue.use(Field);
import { Popup } from 'vant';
Vue.use(Popup);
export default {
    data(){
        return {
            nameaaa:"",
            dizhiaaa:"",
            dianhuaaa:"",
            jiudianzong:[]
        }
    },
    methods:{
       huitui() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
        zhuzhi:function(){
            console.log(this.name,this.dizhi,this.dianhua)
            if(this.name==""){
                alert("Hotel Delivery Address cannot be empty")
            }else if(this.dizhi==""){
                alert("Hotel Name cannot be empty")
            }else if(this.dianhua==""){
                alert("Hotel Phone Number cannot be empty")
            }else{
                this.jiudianzong=[{namea:this.nameaaa,dizhia:this.dizhiaaa,dianhuaa:this.dianhuaaa}]
                this.$router.push({
                path:"/zhaochea",
                query:{
                    jiudianzong:this.jiudianzong 
                }
                })
            }
        }
    }
}
</script>

<style>
@import "../css/tianjiazhuzhi.css";
</style>


