<template>
  <!--乘客添加界面-->
  <div style="background:#ffffff;height:700px;">
    <div class="top-box">
      <div @click="huitui" class="jiantou">
        <img
          style="width:24px;margin-left:10px;margin-top:15px;"
          src="../img/find train-back.png"
          alt
        />
      </div>
      <div class="zhongzi">Add adult</div>
      <div @click="ok" class="youzi">Save</div>
    </div>
    <div class="box-wai">
      <van-cell-group>
        <div style="border-bottom:1px #DCDCDC solid;">
          <van-field v-model="name" placeholder="Surname" />
        </div>
        <div style="border-bottom:1px #DCDCDC solid;">
          <van-field v-model="quanname" placeholder="Given Names" />
        </div>
      </van-cell-group>
      <div style="border-bottom:1px #DCDCDC solid;">
        <van-dropdown-menu>
          <van-dropdown-item v-model="value1" :options="option1" />
        </van-dropdown-menu>
      </div>
      <van-cell-group>
        <div style="border-bottom:1px #DCDCDC solid;">
          <van-field v-model="idbumber" placeholder="ID Number" />
        </div>
        <div style="border-bottom:1px #DCDCDC solid;">
          <van-field v-model="nation" placeholder="Nation" />
        </div>
      </van-cell-group>
    </div>
    <van-popup v-model="show">内容</van-popup>
  </div>
</template>
<style>
@import "../css/chengren.css";
</style>
<script>
import Vue from "vue";
import { Field } from "vant";
Vue.use(Field);
import { Popup } from "vant";
Vue.use(Popup);
export default {
  data() {
    return {
      name: "",
      quanname: "",
      idbumber: "",
      nation: "",
      value1: 0,
      show: false,
      option1: [
        { text: "Passport", value: 0 },
        { text: "ID card", value: 1 },
        { text: "Second-generation ID card", value: 2 },
        { text: "Hong Kong, Macao and Taiwan Pass", value: 3 }
      ]
    };
  },
  mounted() {
    console.log(this.$route.query.list);
  },
  methods: {
    huitui() {
      this.$router.go(-1);
    },
    showPopup() {
      this.show = true;
    },
    // 添加成人票
    ok() {
      console.log(this.$route.query.list);
      for (var i = 0; i < this.option1.length; i++) {
        if (i == this.value1) {
          // console.log(this.option1[i].text)
        }
      }
      // console.log(this.name,this.quanname,this.idbumber,this.nation,this.value1)
      if (this.name == "") {
        alert("Please fill out Surname");
      } else if (this.quanname == "") {
        alert("Please fill out Given Names");
      } else if (this.idbumber == "") {
        alert("Please fill out ID Number");
      } else if (this.nation == "") {
        alert("Please fill out Nation");
      } else {
        if (
          this.$route.query.list == undefined ||
          this.$route.query.list == "undefined"
        ) {
          var dd = [
            {
              name: this.name,
              qname: this.quanname,
              id: this.idbumber,
              guo: this.nation,
              chengren: "Adult",
              value: this.value1
            }
          ];
          // var xinxi=this.$route.query.list.push(dd)
          this.$router.push({
            path: "/tianjia",
            query: {
              // name:this.name,
              // qname:this.quanname,
              // id:this.idbumber,
              // guo:this.nation
              xinxi: dd,
              xinxii: this.$route.query.listt
            }
          });
        } else {
          var dd = {
            name: this.name,
            qname: this.quanname,
            id: this.idbumber,
            guo: this.nation,
            chengren: "Adult",
            value: this.value1
          };
          var xinxi = this.$route.query.list.push(dd);
          this.$router.push({
            path: "/tianjia",
            query: {
              // name:this.name,
              // qname:this.quanname,
              // id:this.idbumber,
              // guo:this.nation
              xinxi: this.$route.query.list,
              xinxii: this.$route.query.listt
            }
          });
        }
      }
    }
  }
};
</script>



