<template>
<!--火车票预订界面-->
  <div class="zhc-box">
    <div class="zhc-top-box">
      <div class="zhc-top-jt">
        <img @click="huitui" class="zhc-top-img" src="../img/home-back@2x.png" />
      </div>
      <div class="zhc-top-zi">
        Beijing
        <img style="width:20px;margin-bottom:4px;" src="../img/beijing-shanghai@2x.png" /> Shanghai
      </div>
      <div class="zhc-top-d-b">
        <div @click="jian()" class="befor">Before</div>
        <div class="zhc-top-d">{{this.ri}}  {{this.rir}}  {{this.xingqi}}</div>
        <div @click="jia()" class="befor">After</div>
      </div>
    </div>
    <!-- 车票 -->
    <div id="ac">
      <div v-for="(item,index) in piao" :key="index">
        <div @click="xianqing(item)" class="piao" v-if="!item.show">
          <div class="piao-nei">
            <!-- 票的上半部分 -->
            <div class="piao-top">
              <div ref="a" class="piao-a">{{item.train_code}}</div>
              <div class="piao-b">
                <span>{{chuming}}</span>
                <span>{{item.start_time}}</span>
              </div>

              <div class="piao-c">
                <div class="piao-d"></div>
                {{item.run_time}}
                <div class="piao-d"></div>
              </div>

              <div class="piao-e">
                <span>{{daoming}}</span>
                <span>{{item.arrive_time}}</span>
              </div>
            </div>
            <!-- 票的下半部分 -->
            <div class="piao-bottom">
              <div class="pb-a">
                <span
                  class="aa"
                  ref="zZz"
                >1st Class:USD{{Math.round(item.ydz_price*1/item.ExchangeRate*100)/100}}</span>
                <span class="zz" ref="zZ">1st Class:{{item.ydz_num}}</span>
                <span class="bb" ref="zZ">Business Cls.:{{item.swz_num}}</span>
                <span class="bbb" ref="zZz">Business Cls.:USD{{item.swz_price}}</span>
              </div>
              <div
                class="pb-b"
                ref="zZz"
              >2nd Class:USD{{Math.round(item.edz_price*1/item.ExchangeRate*100)/100}}</div>
              <div class="zzz" ref="zZ">2nd Class:{{item.edz_num}}</div>
            </div>
          </div>
        </div>
      </div>

      <!-- <yd-button size="large" type="warning" @click.native="show2 = true">下部弹出</yd-button> -->
      <yd-popup v-model="show2" position="bottom" height="60%">
        <div class="shai-box">
          <yd-button type="warning" id="dian" @click.native="quxiao">Cancel</yd-button>
          <yd-button type="warning" id="dian1" @click.native="quxiao">Filter</yd-button>
          <yd-button type="warning" id="dian2" @click.native="queding">Conform</yd-button>
        </div>
        <!-- 点击 -->
        <yd-checkbox-group v-model="checkbox2">
          <div class="shai-biao">
            <div class="gang gangz"></div>
            <div class="biaot">Model Selection</div>
            <div class="gang"></div>
          </div>
          <div class="diandian">
            <div style="width:50%;">
              <yd-checkbox val="1">High-speed rail</yd-checkbox>
              <yd-checkbox class="xuan-ttp" val="3">Ordinary(Z/T/K)</yd-checkbox>
            </div>
            <div style="width:50%;">
              <yd-checkbox val="2">Motor train</yd-checkbox>
              <yd-checkbox class="xuan-ttp" val="4">Others(L/Y More)</yd-checkbox>
            </div>
          </div>
        </yd-checkbox-group>
        <yd-checkbox-group v-model="checkbox3">
          <div class="shai-biao">
            <div class="gang gangz"></div>
            <div class="biaot">Departure Time</div>
            <div class="gang"></div>
          </div>
          <div class="diandian">
            <div style="width:50%;">
              <yd-checkbox val="5">00:00-06:00</yd-checkbox>
              <yd-checkbox class="xuan-ttp" val="6">12:00-18:00</yd-checkbox>
            </div>
            <div style="width:50%;">
              <yd-checkbox val="7">06:00-12:00</yd-checkbox>
              <yd-checkbox class="xuan-ttp" val="8">18:00-24:00</yd-checkbox>
            </div>
          </div>
        </yd-checkbox-group>
      </yd-popup>

      <!-- 筛选部分 -->
      <div class="shaixuan">
        <div @click="zuizao()" class="shaixb shaixbb">Earliest↑</div>
        <div @click="zuiduan()" class="shaixba">Time Consuming↑</div>
        <div class="shaixb shaixbb">
          <yd-button id="sp" size="large" type="warning" @click.native="show2 = true">Filter</yd-button>
        </div>
        <div @click="showw()" class="shaixb shaixbb">Show Fare</div>
      </div>
    </div>
    <!-- 票的详情 -->
    <div class="piao-nei">
      <div class="piao-top" id="xiangq">
        <!-- <div ref="a" class="piao-a">
                        {{xiangd}}
        </div>-->
        <div id="xianga">
          <span>{{chuming}}</span>
          <span>{{xiangc}}</span>
          <span>{{this.ri}} {{this.rir}} {{this.xingqi}}</span>
        </div>

        <div style="width:25%;height:50%">
          <div class="xiangc">
            <div class="piao-d"></div>
            {{xianga}}
            <div class="piao-d"></div>
          </div>
          <div class="xiangqinga">{{xiangd}}</div>
        </div>

        <div class="xiangb">
          <span>{{daoming}}</span>
          <span>{{xiangf}}</span>
          <span>{{this.ri}} {{this.rirr}} {{this.xiaxia}}</span>
        </div>
      </div>
    </div>
    <div class="maipiao">
      <div class="zuoci">
        <span>1st Class</span>
        <span class="qiana">usd{{Math.round(yideng*100)/100}}</span>
        <span>{{yishu}}</span>
        <span @click="yi()" class="dianmaia">Book</span>
      </div>
      <div class="zuoci">
        <span>2nd Class</span>
        <span class="qianb">usd{{Math.round(erdeng*100)/100}}</span>
        <span>{{ershu}}</span>
        <span @click="er()" class="dianmaib">Book</span>
      </div>
      <div class="zuoci zuociz">
        <span>Deluxe Soft Sleeper</span>
        <span class="qianc">usd{{Math.round(shangwu*100)/100}}</span>
        <span>{{shangshu}}</span>
        <span @click="shangwude()" class="dianmaic">Book</span>
      </div>
    </div>
  </div>
</template>
<style>
@import "../css/zhaohuoche.css";
</style>
<script>
import Vue from "vue";
import { Popup } from "vue-ydui/dist/lib.rem/popup";
/* 使用px：import {Popup} from 'vue-ydui/dist/lib.px/popup'; */
import { CheckBox, CheckBoxGroup } from "vue-ydui/dist/lib.rem/checkbox";
/* 使用px：import {CheckBox, CheckBoxGroup} from 'vue-ydui/dist/lib.px/checkbox'; */

Vue.component(CheckBox.name, CheckBox);
Vue.component(CheckBoxGroup.name, CheckBoxGroup);

Vue.component(Popup.name, Popup);
export default {
  data() {
    return {
      piao: [],
      chuming: "",
      daoming: "",
      shi: [],
      dqr: "",
      chufacode: "",
      daodacode: "",
      show: true,
      shu: 0,
      ri: "",
      rir: "",
      xingqi: this.$route.query.we,
      zanshibao: "",
      dd: "",
      show2: false,
      checkbox2: [],
      checkbox3: [],
      xyshow: true,
      xianga: "",
      xiangb: "",
      xiangc: "",
      xiangd: "",
      xiange: "",
      xiangf: "",
      yideng: "",
      erdeng: "",
      shangwu: "",
      ershu: "",
      yishu: "",
      shangshu: "",
      chepiaoshuju: [],
      rirr: "",
      xiaxia: "",
      yiyi: "",
      erer: "",
      shsh: "",
      cm: "",
      cc: "",
      dm: "",
      dc: ""
    };
  },
  methods: {
    //回退-路由跳转主界面未完成
    huitui() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    }, huitui() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    /*加入判断，如果订票日期距离当前日期超过30天，那么book按钮设置为禁用
    test() {
      var date1 = new Date();
      var date2 = new Date(date1);
      date2.setDate(date1.getDate() + 30);
      var time1 = date2.getMonth() + 1;
      var time2 =date2.getDate();
      alert(time1+'-' +time2);
      if (this.ri == time1&&this.rir==time2) {
         document.getElementById("maipiao").setAttribute("disabled", true);
      }
    },*/
    // 票的详情页
    xianqing(item) {
      sessionStorage.setItem("cm", item.from_station_name);
      sessionStorage.setItem("cccc", item.from_station_code);
      sessionStorage.setItem("dm", item.to_station_name);
      sessionStorage.setItem("dc", item.to_station_code);
      sessionStorage.setItem("huilv", item.ExchangeRate);
      // console.log(index)
      if (item.arrive_days == "0") {
        this.rirr = this.rir;
        this.xiaxia = this.xingqi;
      } else {
        this.rirr = this.rir * 1 + item.arrive_days * 1;
        var c = ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"];
        console.log("这事详情的星期：" + this.xingqi);
        for (var i = 0; i < c.length; i++) {
          if (c[i] == this.xingqi) {
            var xia = (i + 1) % 7;
            // console.log(c[i],typeof i)
            this.xiaxia = c[xia];
          }
        }
      }
      console.log(item);

      this.yiyi = item.ydz_price;
      this.erer = item.edz_price;
      this.shsh = item.swz_price;
      this.chepiaoshuju = item;
      document.getElementById("ac").style.display = "none";
      document.getElementsByClassName("maipiao")[0].style.display = "block";
      document.getElementById("xiangq").style.display = "flex";
      this.xianga = item.train_code;
      this.xiangb = item.from_station_name;
      this.xiangc = item.start_time;
      this.xiangd = item.run_time;
      this.xiange = item.to_station_name;
      this.xiangf = item.arrive_time;
      this.yideng = (item.ydz_price * 1) / item.ExchangeRate;
      this.erdeng = (item.edz_price * 1) / item.ExchangeRate;
      this.shangwu = (item.swz_price * 1) / item.ExchangeRate;
      if (item.edz_num == "0") {
        this.ershu = "No";
        document.getElementsByClassName("dianmaib")[0].style.backgroundColor =
          "#C8C9CC";
      } else {
        this.ershu = "Yes";
      }
      if (item.ydz_num == "0") {
        this.yishu = "No";
        document.getElementsByClassName("dianmaia")[0].style.backgroundColor =
          "#C8C9CC";
      } else {
        this.yishu = "Yes";
      }
      if (item.swz_num == "0") {
        this.shangshu = "No";
        document.getElementsByClassName("dianmaic")[0].style.backgroundColor =
          "#C8C9CC";
      } else {
        this.shangshu = "Yes";
      }
    },
    yi() {
      sessionStorage.setItem("piaojia", this.yideng);
      sessionStorage.setItem("piaop", this.yiyi);
      var qian = document.getElementsByClassName("qiana")[0].innerHTML;
      console.log(qian);
      if (this.yishu == "No") {
        // console.log(this.yishu)
        return false;
      } else {
        // console.log(this.chepiaoshuju)
        this.$router.push({
          path: "/zhaochea",
          query: {
            chuming: this.chuming,
            daoming: this.daoming,
            chufacode: this.chufacode,
            daodacode: this.daodacode,
            chepiaoshuju: this.chepiaoshuju,
            yue: this.ri,
            ri: this.rir,
            xingqi: this.xingqi,
            dri: this.rirr,
            dxingqi: this.xiaxia,
            qian: qian,
            classs: "1nd Class"
          }
        });
      }
    },
    er() {
      sessionStorage.setItem("piaojia", this.erdeng);
      sessionStorage.setItem("piaop", this.erer);
      var qian = document.getElementsByClassName("qiana")[0].innerHTML;
      if (this.ershu == "No") {
        // console.log(this.ershu)
        return false;
      } else {
        // console.log("ccc")
        this.$router.push({
          path: "/zhaochea",
          query: {
            chuming: this.chuming,
            daoming: this.daoming,
            chufacode: this.chufacode,
            daodacode: this.daodacode,
            chepiaoshuju: this.chepiaoshuju,
            yue: this.ri,
            ri: this.rir,
            xingqi: this.xingqi,
            dri: this.rirr,
            dxingqi: this.xiaxia,
            qian: qian,
            classs: "2nd Class"
          }
        });
      }
    },
    shangwude() {
      sessionStorage.setItem("piaojia", this.shangwu);
      sessionStorage.setItem("piaop", this.shsh);
      var qian = document.getElementsByClassName("qiana")[0].innerHTML;
      if (this.shangshu == "No") {
        // console.log(this.ershu)
        return false;
      } else {
        // console.log("ccc")
        this.$router.push({
          path: "/zhaochea",
          query: {
            chuming: this.chuming,
            daoming: this.daoming,
            chufacode: this.chufacode,
            daodacode: this.daodacode,
            chepiaoshuju: this.chepiaoshuju,
            yue: this.ri,
            ri: this.rir,
            xingqi: this.xingqi,
            dri: this.rirr,
            dxingqi: this.xiaxia,
            qian: qian,
            classs: "Deluxe Soft Sleeper"
          }
        });
      }
    },
    // 筛选确定,取消
    queding() {
      for (var c = 0; c < this.piao.length; c++) {
        this.piao[c].show = false;
      }
      this.show2 = false;
      // console.log(this.piao)
      for (var e = 0; e < this.piao.length; e++) {
        for (var q = 0; q < this.checkbox2.length; q++) {
          // for(var l=0;l<this.checkbox3.length;l++){

          if (this.checkbox2[q] == 1) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var c = this.piao[e].train_type;
              if (c == "G") {
                this.piao[e].show = false;
                // console.log(this.piao[e])
                // console.log("这里等于1")
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox2[q] == 3) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var c = this.piao[e].train_type;
              if (c == "Z" || c == "T" || c == "K") {
                this.piao[e].show = false;
                // console.log("这里等于3")
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox2[q] == 2) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var c = this.piao[e].train_type;
              if (c == "D") {
                this.piao[e].show = false;
                // console.log("这里等于3")
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox2[q] == 4) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var c = this.piao[e].train_type;
              if (c == "L" || c == "Y") {
                this.piao[e].show = false;
                // console.log("这里等于3")
              } else {
                this.piao[e].show = true;
              }
            }
          }

          if (this.checkbox3[0] == 5) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var kai = this.piao[e].start_time;
              var kaishi = kai.slice(0, 2) * 1;
              if (kaishi >= 0 && kaishi <= 6) {
                this.piao[e].show = false;
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox3[0] == 7) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var kai = this.piao[e].start_time;
              var kaishi = kai.slice(0, 2) * 1;
              if (kaishi >= 6 && kaishi <= 12) {
                this.piao[e].show = false;
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox3[0] == 6) {
            for (var i = 0; i < this.$refs.a.length; i++) {
              var kai = this.piao[e].start_time;
              var kaishi = kai.slice(0, 2) * 1;
              if (kaishi >= 12 && kaishi <= 18) {
                this.piao[e].show = false;
              } else {
                this.piao[e].show = true;
              }
            }
          }
          if (this.checkbox3[0] == 8) {
            console.log(this.checkbox2[0]);
            for (var i = 0; i < this.$refs.a.length; i++) {
              var kai = this.piao[e].start_time;
              var kaishi = kai.slice(0, 2) * 1;
              if (kaishi >= 18 && kaishi <= 24) {
                this.piao[e].show = false;
              } else {
                this.piao[e].show = true;
              }
            }
          }
          // }
        }
      }

      for (var e = 0; e < this.piao.length; e++) {}
    },
    quxiao() {
      this.show2 = false;
    },
    // 最早的
    zuizao() {
      document.getElementsByClassName("shaixbb")[0].style.color = "#1F97BA";
      document.getElementsByClassName("shaixba")[0].style.color = "#FFFFFF";
      var compare = function(prop) {
        return function(obj1, obj2) {
          var val1 = obj1[prop];
          var val2 = obj2[prop];
          if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
            val1 = Number(val1);
            val2 = Number(val2);
          }
          if (val1 < val2) {
            return -1;
          } else if (val1 > val2) {
            return 1;
          } else {
            return 0;
          }
        };
      };
      console.log(this.piao.sort(compare("start_time")));
    },
    // 时间最短的
    zuiduan() {
      document.getElementsByClassName("shaixbb")[0].style.color = "#FFFFFF";
      document.getElementsByClassName("shaixba")[0].style.color = "#1F97BA";
      var compare = function(prop) {
        return function(obj1, obj2) {
          var val1 = obj1[prop];
          var val2 = obj2[prop];
          if (!isNaN(Number(val1)) && !isNaN(Number(val2))) {
            val1 = Number(val1);
            val2 = Number(val2);
          }
          if (val1 < val2) {
            return -1;
          } else if (val1 > val2) {
            return 1;
          } else {
            return 0;
          }
        };
      };

      console.log(this.piao.sort(compare("run_time_minute")));
    },
    showw() {
      if (!this.show) {
        for (var i = 0; i < this.$refs.zZ.length; i++) {
          this.$refs.zZ[i].style.display = "block";
        }
        for (var i = 0; i < this.$refs.zZz.length; i++) {
          this.$refs.zZz[i].style.display = "none";
        }
        this.show = true;
      } else {
        for (var i = 0; i < this.$refs.zZ.length; i++) {
          this.$refs.zZ[i].style.display = "none";
        }
        for (var i = 0; i < this.$refs.zZz.length; i++) {
          this.$refs.zZz[i].style.display = "block";
        }
        this.show = false;
      }
    },
    jian() {
      // alert("meiwenti")
      var date = new Date();
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate;
      console.log(this.zanshibao == currentdate);
      console.log(this.zanshibao);
      if (this.zanshibao == currentdate) {
        return;
      } else {
        function addDays(date, days, seperator = "-") {
          let oDate = new Date(date).valueOf();
          let nDate = oDate + days * 24 * 3600 * 1000;
          nDate = new Date(nDate);
          let y = nDate
            .getFullYear()
            .toString()
            .padStart(2, 0);
          let m = (nDate.getMonth() + 1).toString().padStart(2, 0);
          let d = nDate
            .getDate()
            .toString()
            .padStart(2, 0);
          return `${y}${seperator}${m}${seperator}${d}`;
        }
        var a = ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"];
        var yue = [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sept",
          "Oct",
          "Nov",
          "Dec"
        ];
        // this.shu = 1
        this.shu--;
        // console.log(this.shu)
        var jia = addDays(this.dqr, this.shu);
        this.zanshibao = jia;
        // console.log(this.shu)
        // console.log(jia)
        this.$http
          .get(
            "http://123.57.18.91:1002/api/ticket/queryV2?train_date=" +
              jia +
              "&from_station=" +
              this.chufacode +
              "&to_station=" +
              this.daodacode +
              "&terminal=1&time_stamp={time_stamp}&random=1234&sign={sign}"
          )
          .then(function(res) {
            console.log(res);
            if (
              res.body.code == 0 &&
              res.body.message == "超过查询频次限制（21）"
            ) {
              alert("查得太频繁了");
            } else {
              this.piao = res.body.data;
            }
            var jiadom = jia.split("-");
            console.log(jiadom);
            var yingy = yue[jiadom[1] * 1 - 1];
            console.log(yingy);
            this.ri = yingy;
            this.rir = jiadom[2];
            // alert(a[jiadom[2]*1%7+3])
            this.xingqi = a[(((jiadom[2] * 1) % 7) + 3) % 7];
          });
        if (this.zanshibao == currentdate) {
          return;
        }
      }
    },
    jia() {
      function addDays(date, days, seperator = "-") {
        let oDate = new Date(date).valueOf();
        let nDate = oDate + days * 24 * 3600 * 1000;
        nDate = new Date(nDate);
        let y = nDate
          .getFullYear()
          .toString()
          .padStart(2, 0);
        let m = (nDate.getMonth() + 1).toString().padStart(2, 0);
        let d = nDate
          .getDate()
          .toString()
          .padStart(2, 0);
        return `${y}${seperator}${m}${seperator}${d}`;
      }
