<template>
  <div>
    <div class="top-box">
      <span @click="back">
        <img
          style="width:22px;float:left;margin-top:17px;margin-left:10px;"
          src="../img/find train-back.png"
        />
      </span>
      <span style="margin-left:-10px;">Help?</span>
    </div>
    <div class="dian-box">
      <div>
        <router-link to="/A">
          How to Book Tickets?
          <img src="../img/book_ed_expand@2x.png" alt />
        </router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/B">How to know my Booking Status?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/C">How to Collect Tickets?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/D">How to Choose Train?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/E">Types & Seat Classes?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/F">How to Read the Tickets?</router-link>
      </div>
      <div>
        <router-link to="/G">
          <img src="../img/book_ed_expand@2x.png" alt />
          How to Take Train in China?
        </router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/H">How to Take Train in China?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/K">How to Change Tickets?</router-link>
      </div>
      <div>
        <img src="../img/book_ed_expand@2x.png" alt />
        <router-link to="/M">How to Cancel Tickets Booking Q&A?</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    back() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "HelloWorld" });
        return false;
      } else {
        this.$router.go(-1);
      }
    }
  }
};
</script>

<style>
@import "../css/help.css";
</style>


