<template>
    <div class="bigbox">
        <h1>zhaohuoche.vue</h1>
        <div class="top-tou">
            <div class="topbiaoti"> <img style="width:20px;float:left;" src="../img/home-back@2x.png"  @click="huitui"> Confirmation Details</div>
    <div class="piao-nei" id="piao-nein">
    <div class="piao-top" id="xiangq">
                    <!-- <div ref="a" class="piao-a">
                        {{xiangd}}
                    </div> -->
                    <div id="xianga">
                        <span>{{chuming}}</span>
                        <span>{{chepiaoshuju.start_time}}</span>
                        <span>{{this.ri}}  {{this.rir}}  {{this.xingqi}}</span>
                        <div class="zuocia">
                            <div class="sp1">{{this.classs}}</div>
                            <div class="sp2">{{this.qian}}</div>
                        </div>
                        
                    </div>

                    <div style="width:25%;height:50%">
                    <div class="xiangc" id="xiahua">
                        <div class="piao-d"></div>
                        {{chepiaoshuju.train_code}}
                        <div class="piao-d"></div>
                    </div>
                    <div class="xiangqinga">
                        {{chepiaoshuju.run_time}}
                    </div>
                    </div>
                    
                    <div class="xiangb">
                        <span id="sp1">{{daoming}}</span>
                        <span id="sp2">{{chepiaoshuju.arrive_time}}</span>
                        <span id="sp3">{{this.ri}} {{this.rirr}} {{this.xiaxia}}</span>
                    </div>
                    
                </div>
                </div>
                
<div class="tianjiade">
    <div class="chengkexiangqing" v-for="(item,index) in result" :key="index">
        <div class="keziliao">
                    <div class="mingzi">
                        {{item.qname}} {{item.name}}
                        <span class="biao">{{item.chengren}}</span>
                        <img @click="shanchu(index)" class="shanchu" src="../img/book_add_delete@2x.png" alt="">
                    </div>
                    <span class="idhao">
                            <span style="color:#1F97BA;">Passport</span> {{item.id}}
                    </span>
                    <span class="guo">
                        {{item.guo}}
                    </span>
                </div>
    </div>
    <div class="chengkexiangqing" v-for="(item,index) in resultt" :key="index">
        <div class="keziliao">
                    <div class="mingzi">
                        {{item.qname}} {{item.name}}
                        <span class="biao">{{item.chengren}}</span>
                        <img @click="shanchua(index)" class="shanchu" src="../img/book_add_delete@2x.png" alt="">
                    </div>
                    <span class="idhao">
                            <span style="color:#1F97BA;">Passport</span> {{item.id}}
                    </span>
                    <span class="guo">
                        {{item.guo}}
                    </span>
                </div>
    </div>
        <!-- 添加票 -->
        <div @click="tianchr" class="tianjiaanniu" style="display:flex;">
            <!-- <span @click="tianchr">Add adult</span>
            <span></span>
            <span>Add child</span> -->
            <div style="height:100%;width:20px;display:inline-block;"><img style="width:18px;" src="../img/矢量智能对象@3x.png" alt=""></div>
            <div style="height:100%;width:50%;display:inline-block;">
                Choose passenger

            </div>
        </div>
        <!-- 快递 -->
        <dir @click="kuaidi" class="kuaidia">
            <div class="immtu">
                <img class="tu" src="../img/kuaidi.png" alt="">
            </div>
            <div class="zii">
                <div class="ziimmtubiao">
                    Express ticket collection
                    <img class="tu2" src="../img/book_ed_expand@2x.png" alt="">
                </div>
                <div class="danzi">
Can be delivered to a designated location without 
the need for a station to queue for tickets.
                        
                </div>
            </div>
            
        </dir>
        <!-- 隐藏的快递信息 -->
        <div @click="kuaidi" class="kuaidixina">
            <div v-for="(item,index) in jiudian" :key="index" class="zii">
                <div class="ziimmtubiao">
                    {{item.name}}
                    <img class="tu2" src="../img/book_ed_expand@2x.png" alt="">
                </div>
                <div class="danzi">
                    {{item.dizhi}}
                    lianxiren:{{item.ydname}}
                </div>
            </div>
        </div>
        <div class="kuaidixiangq">
            <div @click="jiudiandizhi" class="jichu"><span class="jichuu">Deliver to my China hotel</span><img class="tu3" src="../img/book_ed_expand@2x.png" alt=""></div>
            <div @click="jiadiandizhi" class="jichu"><span class="jichuu">Deliver to a personal address in China</span><img class="tu3" src="../img/book_ed_expand@2x.png" alt=""></div>
            
            
        </div>
        <div class="buzhidaoshazi">
            
                            Click on book to indicate that you have read and 
agreed <a href="#" style="color:#1F97BA;">terms and conditions.</a>
                        
        </div>
        </div>
    </div>
    <!-- 最下面点击按钮 -->
    <div class="dianjizong">
        <div class="xiangxi">
            <div class="qiana">Adult train tickets x{{chengshu}}  <span class="qianq">USD{{qianqian}}</span> </div>
            <div class="qianb">Child train ticket x{{yingerpiao}} <span class="qianq">USD{{ertongqian}}</span> </div>
            <div class="qianc">Service Fee x{{fuwufei}} <span class="qianq">USD{{fuwufei*5}}</span> </div>
            <div class="qiand">Courier fee <span class="qianq">USD{{kdf}}</span> </div>
        </div>
        <div class="maipiaodianji">
            <div style="color:#666666;font-size:14px;">Total order</div>
            <div class="dianjixy" @click="xiangqing">
                &nbsp;&nbsp;
                USD <span style="font-size:21px;color:#1F97BA;">{{cc}}</span>
                <img class="xialatu" style="width:16px;" src="../img/book_price_expand@2x.png" alt="">
            </div>
            <div @click="dianjihoumai" class="dianjihoumai">Book</div>
        </div>
    </div>  

    </div>
</template>
<style>
@import "../css/maipiao.css";
@import "../css/zhaohuoche.css";
 
#xiangq{
    display: flex;
}
</style>
<script>

export default {
    data(){
        return {
            chuming:this.$route.query.chuming,
            daoming:this.$route.query.daoming,
            chufacode:this.$route.query.chufacode,
            daodacode:this.$route.query.daodacode,
            chepiaoshuju:this.$route.query.chepiaoshuju,
            ri:this.$route.query.yue,
            rir:this.$route.query.ri,
            xingqi:this.$route.query.xingqi,
            rirr:this.$route.query.dri,
            xiaxia:this.$route.query.dxingqi,
            qian:this.$route.query.qian,
            dadada:[],
            xiangc:"",
            xianga:"",
            xiangb:"",
            xiangf:"",
            xiangd:"",
            classs:this.$route.query.classs,
            xysh:false,
            qianqian:"",
            yingerpiao:0,
            fuwufei:0,
            kuaidifei:"",
            result:this.$route.query.result,
            resultt:this.$route.query.resultt,
            chengshu:0,
            youdi:true,
            jiudian:"",
            ertongqian:"",
            qianzong:"",
            kdf:0,
            cc:""
        }
    },
    mounted(){
        //this.result和this.resultt数值为空-强行添加会报错 .<length>
        console.info( this.result);
        console.info(this.reresultt);
        console.info(this.chengshu);

        sessionStorage.setItem("dizhi","1")
        // console.log(typeof this.classs)
        if(this.classs!=undefined){
            sessionStorage.setItem("classs",this.classs)
        } 
        // console.log(sessionStorage.getItem("classs"))
        // 判断酒店地址
        if(this.$route.query.jiudianzong!=undefined){
            this.jiudian=this.$route.query.jiudianzong
            var jiudian = JSON.stringify(this.$route.query.jiudianzong)
            console.log(jiudian)
            sessionStorage.setItem("jiudian",jiudian)
        }else{
            var jiudian = sessionStorage.getItem("jiudian")
            this.jiudian = JSON.parse(jiudian)
            console.log(this.jiudian)
        }
        console.log(this.jiudian +  "")
        
        // console.log(document.getElementsByClassName("kuaidia")[0])
        if(this.result!=undefined){
            this.chengshu=this.chengshu+this.result.length
            console.log(this.resultt)
            this.yingerpiao=this.yingerpiao+this.resultt.length
            this.fuwufei=this.chengshu+this.yingerpiao
        }
        if(document.getElementsByClassName("kuaidia" )[0]!=undefined){
        if(this.jiudian==undefined){
            console.log(document.getElementsByClassName("kuaidia")[0])
            document.getElementsByClassName("kuaidia" )[0].style.display="flex"
            document.getElementsByClassName("kuaidixina")[0].style.display="none"
            console.log("我在else")
        }else{
            console.log("我在else")
            document.getElementsByClassName("kuaidia")[0].style.display="none"
            document.getElementsByClassName("kuaidixina")[0].style.display="block"
        }
        }
        

        if(this.chuming==undefined){
            this.chuming = sessionStorage.getItem("chuming")
            this.daoming = sessionStorage.getItem("daoming")
            this.chufacode = sessionStorage.getItem("chufacode")
            this.daodacode = sessionStorage.getItem("daodacode")
            
            var shuju = sessionStorage.getItem("chepiaoshuju")
            
            this.chepiaoshuju =JSON.parse(shuju)
            this.ri = sessionStorage.getItem("ri")
            this.rir = sessionStorage.getItem("rir")
            this.xingqi = sessionStorage.getItem("xingqi")
            this.rirr = sessionStorage.getItem("rirr")
            this.xiaxia = sessionStorage.getItem("xiaxia")
            this.qian = sessionStorage.getItem("qian")
        }
        this.qianqian=this.qian.slice(3)
        this.qianqian=this.qianqian*1*this.chengshu
        this.ertongqian = this.qianqian*1/2*(this.yingerpiao*1)
        this.qianzong = this.qianqian+this.ertongqian+this.fuwufei*5+this.kdf
        // alert(this.qianzong)
        console.log(this.qianqian,this.ertongqian,this.fuwufei,this.kdf);
         //length出错
        /*if(this.resultt.length==0){
            document.getElementsByClassName("qianb")[0].style.display="none"
        }*/
        // if(this.fuwufei==""){
        //     document.getElementsByClassName("qianc")[0].style.display="none"
        // }
        if(this.jiudian==undefined){
            document.getElementsByClassName("qiand")[0].style.display="none"
        }else{
            document.getElementsByClassName("qiand")[0].style.display="block"
        }
        //length出错
        // for(var i=0;i<this.result.length;i++){
         
       // }
        if(this.result==undefined){
            if(sessionStorage.getItem("cheng")!=undefined){}
        }
        
        if(this.fuwufei==0){
            document.getElementsByClassName("qianc")[0].style.display="none"
        }else{
            document.getElementsByClassName("qianc")[0].style.display="block"
        }
        if(this.fuwufei==0){
            document.getElementsByClassName("qianc")[0].style.display="none"
        }else{
            document.getElementsByClassName("qianc")[0].style.display="block"
        }
        // this.cc = Math.floor(this.qianzong*1 * 100) / 100
        // sessionStorage.setItem("cc",this.cc)
    },
    updated(){
        console.log(this.jiudian)
        if(this.jiudian!="[object Object]"&&this.jiudian!=null){
            this.kdf=0
            document.getElementsByClassName("qiand")[0].style.display="block"
            // alert("cheng")
        }else{
            this.kdf=5
            document.getElementsByClassName("qiand")[0].style.display="none"
        }
        if(this.chengshu!=0){
            document.getElementsByClassName("qiana")[0].style.display="block"
        }else{
            document.getElementsByClassName("qiana")[0].style.display="none"
        }
        if(this.yingerpiao!=0){
            document.getElementsByClassName("qianb")[0].style.display="block"
        }else{
            document.getElementsByClassName("qianb")[0].style.display="none"
        }




        this.qianzong = this.qianqian+this.ertongqian+this.fuwufei*5+this.kdf
        console.log(this.qianqian,this.ertongqian,this.fuwufei*5,this.kdf)
        if(this.fuwufei==0){
            document.getElementsByClassName("qianc")[0].style.display="none"
        }else{
            document.getElementsByClassName("qianc")[0].style.display="block"
        }
        console.log(this.fuwufei*5,this.qianqian,this.qianzong)
        this.cc = Math.floor(this.qianzong * 1000)/1000
        sessionStorage.setItem("cc",this.cc)
        // 显示邮寄地址
        if(this.jiudian==undefined){
            console.log(this.$route.query.jiudianzong)
            console.log(document.getElementsByClassName("kuaidia")[0])
            document.getElementsByClassName("kuaidia" )[0].style.display="flex"
            document.getElementsByClassName("kuaidixina")[0].style.display="none"
            document.getElementsByClassName("qiand")[0].style.display="none"
        }else{
            document.getElementsByClassName("kuaidia")[0].style.display="none"
            document.getElementsByClassName("kuaidixina")[0].style.display="block"
            console.log("我在else")
            document.getElementsByClassName("qiand")[0].style.display="block"
            this.kuaidifei=30
        }
        //length出错
       // if(this.resultt.length==0){
       //     console.log(document.getElementsByClassName("qianb")[0])
        //    document.getElementsByClassName("qianb")[0].style.display="none"
        //}
        // if(this.fuwufei==""){
        //     document.getElementsByClassName("qianc")[0].style.display="none"
        // }
        
        
    },
    methods:{
        huitui() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/zhaohuoche" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
        // 发送数据
        dianjihoumai(){
            console.log(this.resultt)
            this.$router.push({
                path:"/zhifu",
                query:{
                    chuming:this.chuming,
                    daoming:this.daoming,
                    chufacode:this.chufacode,
                    daodacode:this.daodacode,
                    chepiaoshuju:this.chepiaoshuju,
                    ri:this.ri,
                    rir:this.rir,
                    xingqi:this.xingqi,
                    rirr:this.rirr,
                    xiaxia:this.xiaxia,
                    qian:this.qian,
                    classs:this.classs,
                    result:this.result,
                    resultt:this.resultt,
                    qianzong:this.qianzong,
                    // 这是费用明细
                    chengshu:this.chengshu,
                    qianqian:this.qianqian,
                    yingerpiao:this.yingerpiao,
                    ertongqian:this.ertongqian,
                    fuwufei:this.fuwufei,
                    kdf:this.kdf,
                    // 这是酒店
                    jiudian:this.jiudian
                }
            })
        },
        // 快递
        kuaidi(){
            if(this.youdi==true){
                document.getElementsByClassName("tu2")[0].style.transform="rotate(90deg)"
                document.getElementsByClassName("kuaidixiangq")[0].style.display="block"
                this.youdi=false
                console.log(this.youdi)
            }else if(this.youdi==false){
                console.log("this.youdi")
                document.getElementsByClassName("tu2")[0].style.transform="rotate(0deg)"
                document.getElementsByClassName("kuaidixiangq")[0].style.display="none"
                this.youdi=true
            }
            
            
        },
        // 添加地址
        jiudiandizhi(){
            sessionStorage.setItem("dizhi","2")
            this.$router.push({
                path:"/dizhiye"
            })
        },
        jiadiandizhi(){
            sessionStorage.setItem("dizhi","3")
            this.$router.push({
                path:"/tianjiazhuzhi"
            })
        },
        // 添加成人票
        tianchr(){
            this.$router.push({
                path:"/tianjia",
                query:{
                    
                }
            })
        },
        xiangqing(){
            if(this.xysh==true){
            document.getElementsByClassName("xiangxi")[0].style.height="0"
            document.getElementsByClassName("xialatu")[0].style.transform="rotate(0deg)"
            console.log(document.getElementsByClassName("xialatu")[0].style.transform)
            this.xysh=false
            }else if(this.xysh==false){
            document.getElementsByClassName("xiangxi")[0].style.height="137px"
            document.getElementsByClassName("xialatu")[0].style.transform="rotate(180deg)"
            this.xysh=true
            }
        },
        shanchu(index){
            console.log(index)
            this.result.splice(index,1)
            this.qianqian=this.qianqian*1/this.chengshu
            this.chengshu=this.chengshu-1
            this.qianqian=this.qianqian*1*this.chengshu
            this.fuwufei=this.chengshu+this.yingerpiao
        },
        shanchua(index){
            this.resultt.splice(index,1)
            // this.qianqian=this.qianqian*1/this.chengshu
            this.yingerpiao=this.yingerpiao-1
            // this.qianqian=this.qianqian*1*this.chengshu
            this.fuwufei=this.chengshu+this.yingerpiao
        }
        
    },
    beforeDestroy(){
        sessionStorage.setItem("chuming",this.chuming)
        sessionStorage.setItem("daoming",this.daoming)
        sessionStorage.setItem("chufacode",this.chufacode)
        sessionStorage.setItem("daodacode",this.daodacode)
        
        var shuju = JSON.stringify(this.chepiaoshuju)
        sessionStorage.setItem("chepiaoshuju",shuju)

        sessionStorage.setItem("ri",this.ri)
        sessionStorage.setItem("rir",this.rir)
        sessionStorage.setItem("xingqi",this.xingqi)
        sessionStorage.setItem("rirr",this.rirr)
        sessionStorage.setItem("xiaxia",this.xiaxia)
        sessionStorage.setItem("qian",this.qian)
        // sessionStorage.setItem("classs",this.$route.query.classs)
    }
}
</script>



